import React from 'react';
import config from '../../config';

import logo from '../assets/img/website-icon.png';
export default function Header() {
  return (
    <div id="header">
      <span className="">
        <img src={logo} alt="ảnh lỗi" />
      </span>
      <h1>{config.heading}</h1>
      <p>{config.subHeading}</p>
    </div>
  );
}
